<template>
  <div>
    <Tabs :value="tabName">
      <TabPane label="充值" name="1">
        <ul class="cardList">
          <li class="bankCard" v-for="(item,index) in bankList" :key="index">
            <p class="f20 mb-10 flex-spb">
              <span>{{item.econo_name}}</span>
              <Button size="small" @click="toCharge(item)">充值</Button>
            </p>
            <p style="color:#eeeeee;">储蓄卡</p>
            <p class="f24 mt-20">{{item.econo_account}}</p>
          </li>
        </ul>
      </TabPane>
      <TabPane label="充值记录" name="2">
        <chargeTable></chargeTable>
      </TabPane>
    </Tabs>

  </div>
</template>
<script>
import chargeTable from './chargeTable'
export default {
  components: {
    chargeTable
  },
  data () {
    return {
      page: 1,
      pageSize: 20,
      pageTotal: 0,
      bankList: [],
      tabName: this.$route.query.name ? this.$route.query.name : '1'
    }
  },
  mounted () {
    this.getBanlist()
  },
  methods: {
    async getBanlist () {
      let resData = await this.$post('cgi/get_econo_account', { pos: (this.page - 1) * this.pageSize, offset: this.pageSize })
      if (resData.ret_code == 0) {
        this.bankList = resData.ret_data.data_list
      }
    },
    toCharge (item) {
      this.$router.push({ path: '/chargePage', query: { bankName: item.econo_name, bankUser: item.econo_owner, bankNum: item.econo_account } })
    }
  }
}
</script>
<style lang="less" scoped>
.cardList {
  display: flex;
  flex-wrap: wrap;
  .bankCard {
    margin-right: 30px;
    margin-bottom: 30px;
    width: 330px;
    height: 140px;
    border-radius: 4px;
    padding: 20px 30px;
    color: #fff;
    background: #ff3c3c;
  }
}
</style>