<template>
  <div style="min-height:500px">
    <div class="flex mb-20">
      <div class="">
        时间: <DatePicker v-model="timeZone" type="daterange" :editable="false" placement="bottom-start"
          placeholder="请选择时间" style="width: 240px;margin-left:10px;" @on-change="timeChange">
        </DatePicker>
      </div>
      <div>
        <span class="ml-20">订单编号：</span> <Input style="width: 220px;margin-left:10px" v-model="order_id"></Input>
        <Button type="primary" class="ml-10" @click="searchClick">搜索</Button>
      </div>
    </div>

    <Table :columns="columns" :data="data" border></Table>
    <div class="pageBox">
      <span>
        <span class="mr-10">共{{pageTotal}}条</span>
        每页{{pageSize}}条
      </span>
      <Page style="margin-right:120px" :total="pageTotal" :pageSize="pageSize" @on-change="pageChange"></Page>
      <span></span>
    </div>
  </div>
</template>
<script>
import { number_formate } from '@/utils/number_formate.js'
import { dateFormate } from '@/utils/dateFormate.js'
export default {
  data () {
    return {
      order_id: '',
      columns: [
        {
          title: '订单号',
          key: 'id',
          width: 240
        },
        {
          title: '银行名称',
          key: 'econo_name'
        },
        {
          title: '资金账号',
          key: 'econo_account',
          width: 200
        },
        {
          title: '实充金额',
          key: 'amount',
          render: (h, params) => {
            return h('span', {}, number_formate(params.row.amount))
          },
        },
        {
          title: '充值时间',
          key: 'create_time',
          render: (h, parmas) => {
            if (parmas.row.create_time) {
              return h('div', {}, dateFormate(new Date(parmas.row.create_time * 1000), 'yyyy-MM-dd hh:mm:ss'))
            } else {
              return
            }
          },
        },
        {
          title: '审核时间',
          key: 'approve_time',
          render: (h, parmas) => {
            if (parmas.row.approve_time) {
              return h('div', {}, dateFormate(new Date(parmas.row.approve_time * 1000), 'yyyy-MM-dd hh:mm:ss'))
            } else {
              return
            }

          },
        },
        {
          title: '状态',
          width: 80,
          render: (h, params) => {
            return h('div', {}, this.stateToStr(params.row.state))
          },
        }
      ],
      data: [

      ],
      timeZone: '',
      page: 1,
      pageSize: 10,
      pageTotal: 0
    }
  },
  mounted () {
    this.getChargeData()
  },
  methods: {
    searchClick () {
      this.page = 1
      if ((this.order_id && this.order_id.length == 24) || !this.order_id) {
        this.getChargeData()
      } else {
        this.data = []
        this.pageTotal = 0
      }
    },
    pageChange (page) {
      this.page = page
      this.getChargeData()
    },
    timeChange () {
      let endTime = this.timeZone[1]
      endTime.setHours(23)
      endTime.setMinutes(59)
      endTime.setSeconds(59)
      this.timeZone[1] = endTime
      this.getChargeData()
    },
    async getChargeData () {

      let params = {
        pos: (this.page - 1) * this.pageSize,
        offset: this.pageSize,
        id: this.order_id.trim() ? this.order_id.trim() : undefined
      }
      if (this.timeZone[0]) {
        params.beg_time = this.timeZone[0].getTime() / 1000
        params.end_time = this.timeZone[1].getTime() / 1000
      }
      let resData = await this.$post('cgi/get_recharge', params)
      if (resData.ret_code == 0) {
        this.data = resData.ret_data.recharge_list ? resData.ret_data.recharge_list : []
        this.pageTotal = resData.ret_data.total_num
      }

    },
    stateToStr (state) {
      let str = ''
      switch (state) {
        case 0:
          str = '审核中'
          break;
        case 1:
          str = '成功'
          break;
        case 2:
          str = '失败'
          break;
        default:
          break;
      }
      return str
    }
  }
}
</script>